@import '../../vendor/vars/_vars'
@import '../../vendor/vars/_mixins'
    
.header 
    position: relative
    display: flex
    justify-content: center
    align-items: center
    width: 100%
    padding: 20px 0 15px
    border-bottom: 0.5px solid rgba(255, 255, 255, 0.3)
    flex: 0 1 auto

    /*header-overlay*/
    &__overlay
        display: none
        &_active
            display: block
            position: fixed
            width: 100%
            height: 100vh
            top: 0
            left: 0
            background-color: rgba(0, 0, 0, 0.5)
            z-index: 100
        &-black-head-active
            display: block
            position: fixed
            width: 100%
            height: 281px
            top: 0
            left: 0
            background-color: #000
            z-index: 200


            
    &__logo-link
        display: inline-block
        
    
    &__logo-img
        width: 69.45px
        height: 82.63px

    &__button
        max-width: 181px
        padding: 12px 19px
        overflow: hidden
        color: $color-white
        font-family: Inter
        font-size: 16px
        font-weight: 500
        line-height: 20.8px
        text-align: left
        background: linear-gradient(#0F0F17, #0F0F17) padding-box, linear-gradient(#FB8627, #F5BEF6) border-box
        border-radius: 40px   
        border: 1px solid transparent

        &:hover
            cursor: pointer
            background: linear-gradient(#FB8627, #F5BEF6)

@media screen and ( max-width: 768px  )
    .header
        padding: 20px 10px 10px 10px
        // &__button
        //     width: 
        &__logo-img
            width: 43.65px
            height: 52.13px

.navigation__burger-button
    display: none
    padding: 0
    background-color: transparent
    border: none
    height: 36px
    width: 36px
    &-img
        width: 100%
        background-color: transparent
        border: none

@media screen and ( max-width: 768px  )
    .header__container
        padding-left: 10px
        padding-right: 10px
    .navigation__burger-button
        display: block

    .promo